import React, { useEffect, useState } from 'react';

import {
  Button,
  Divider,
  Grid,
  Paper,
  InputAdornment,
  TextField,
  Typography,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  Box,
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import EditIcon from '@mui/icons-material/Edit';
import SaveIcon from '@mui/icons-material/Save';
import { ThemeProvider } from '@mui/material/styles';

import { editbuttontheme, savebuttontheme, sx } from './styles';

const UserManageScreen = () => {
  console.log('UserManageScreen');

  const [userCount, setUserCount] = useState(0);
  const [searchKeyword, setSearchKeyword] = useState('');

  useEffect(() => {
    setUserCount(0);
  }, []);

  const handleSearchKeyword = event => {
    setSearchKeyword(event.target.value);
  };

  return (
    <>
      <Grid container sx={sx.gridtop}>
        <Grid item xs={12}>
          <Paper sx={sx.paper}>
            <Typography sx={sx.typotitle}> </Typography>
            <Divider />
            <TextField
              margin="normal"
              variant="standard"
              InputProps={{
                disableUnderline: true,
                placeholder: ' ID    .',
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon sx={sx.searchicon} />
                  </InputAdornment>
                ),
              }}
              size="small"
              sx={sx.usersearch}
              value={searchKeyword}
              onChange={handleSearchKeyword}
            />
          </Paper>
        </Grid>
      </Grid>
      <Typography sx={sx.typousercount}> {userCount}</Typography>
      <Grid container sx={sx.gridbottom}>
        <Grid item xs={6}>
          <Paper sx={sx.paper}>
            <TableContainer>
              <Table sx={sx.table}>
                <TableHead sx={sx.tableheaderrow}>
                  <TableRow>
                    <TableCell align="left" sx={sx.tableheader}>
                       ID
                    </TableCell>
                    <TableCell align="left" sx={sx.tableheader}>
                      
                    </TableCell>
                    <TableCell align="left" sx={sx.tableheader}>
                      
                    </TableCell>
                    <TableCell align="left" sx={sx.tableheader}>
                      
                    </TableCell>
                    <TableCell align="left" sx={sx.tableheader}>
                      
                    </TableCell>
                    <TableCell align="left" sx={sx.tableheader}>
                      
                    </TableCell>
                  </TableRow>
                </TableHead>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
        <Grid item xs={6}>
          <Paper sx={sx.paper}>
            <Typography sx={sx.typosubtitle}> </Typography>
            <Box sx={sx.buttonbox}>
              <ThemeProvider theme={editbuttontheme}>
                <Button
                  sx={sx.button}
                  variant="contained"
                  disabled={false}
                  // onClick={handleSearch}
                >
                  <EditIcon />
                  
                </Button>
              </ThemeProvider>
              <ThemeProvider theme={savebuttontheme}>
                <Button
                  sx={sx.button}
                  variant="contained"
                  disabled={false}
                  // onClick={handleInitOptions}
                >
                  <SaveIcon />
                  
                </Button>
              </ThemeProvider>
            </Box>
            <Typography sx={sx.typouserinfo}>ID</Typography>
            <TextField
              margin="normal"
              variant="outlined"
              InputProps={{
                disableUnderline: true,
              }}
              size="small"
              sx={sx.userinfo}
              // value={searchKeyword}
              // onChange={handleSearchKeyword}
            />
          </Paper>
        </Grid>
      </Grid>
    </>
  );
};

export default UserManageScreen;
